<template>
  <div class="wrapper" ref="wrapper">
    <div class="content">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
  name: 'Scroll',
  data() {
    return {
      scroll: null
    }
  },
  props: {
    probeType: {
      type: Number,
      default: 0
    },
    pullUpLoad: {
      type: Boolean,
      default: false
    }
  },
  //
  mounted() {
    //BScroll
     this.scroll = new BScroll(this.$refs.wrapper,{
       click: true,  //better-scroll
       probeType: this.probeType,  //probeType3
       pullUpLoad: this.pullUpLoad   //true
     })
     //position
     this.scroll.on('scroll',position => {
       this.$emit('getPosition', position);
     })
     //
     this.scroll.on('pullingUp',() => {
       this.$emit('pullUpLoad')
     })
  },
  methods: {
    //001s
    scrollTo(x, y, time=1000) {
      this.scroll.scrollTo(x, y, time); 
    },
    refresh() {
      this.scroll.refresh()
    }
  }
}
</script>

